.card {
    margin: 10px;
    border: 0px solid rgb(54, 54, 54);
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 6px 18px -9px rgb(0, 0, 0, 0.75);
    transition: transform 100ms ease-in;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.07);
}

.card > img {
    object-fit: fill;
    width: 100%;
    max-width: 400px;
    max-height: 400px;
}

.card__info {
    margin-top: -9px;
    border-radius: 10px;
    padding:20px;
    padding-top: 20px;
    border: 1;
}

.card__info > h2 {
    font-size: 18px;
    font-weight: 600;
}

.card__info > h4 {
    font-size: 14px;
    font-weight: 300;
    margin-top: 8px;
    margin-bottom: 8px;
}